body {
  font-family: monospace;
}

.app {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: #dadbd3;
}

.app__body {
  display: flex;
  background-color: #ededed;
  margin-top: 10px;
  height: 90vh;
  width: 90vw;
  border-radius: 10px;
  box-shadow: -1px 4px 20px 6px rgba(0, 0, 0, 0.75);
}

@media only screen and (max-width: 600px) {
  .app__body{
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 98vw;
  }
  
}